<template>
    <div class="present">
        <ul class="routes">
            <li>
                <router-link to="/">首页</router-link>
            </li>
            <li>
                <router-link to="/money">我的余额</router-link>
            </li>
            <li>
                <router-link to="/money/exchange">卡券兑换</router-link>
            </li>
        </ul>
        <div class="step step-1" v-if="step===1">

            <div class="step-form">
                <h2 class="ac bold">卡券兑换</h2>
                <form>
                    <table class="row clearfix">
                        <tr class="row col-12">
                            <td class="t-tile col-2">卡&nbsp;&nbsp;&nbsp;&nbsp;号：</td>
                            <td class="t-content col-10"><input v-model="account" placeholder="充值卡卡号" type="text"></td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">密&nbsp;&nbsp;&nbsp;&nbsp;码：</td>
                            <td class="t-content col-10"><input v-model="pwd" placeholder="请输入充值卡密码" type="text"></td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">验证码：</td>
                            <td class="t-content col-10">
                                <input class="account-input" v-model="sms_code" type="text" placeholder="请验证手机验证码">
                                <a class="button ac" v-if="cardcheck">
                                    <Coder :phone="userinfo.mobile"></Coder>
                                </a>
                                <a class="button ac cursor" v-else>
                                    获取验证码
                                </a>
                            </td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">&nbsp;&nbsp;</td>
                            <td class="t-content col-10">
                                <div class="net-step bold ac cursor" :class="{disabled:!allInput}" @click="CheckUserInfo">下一步</div>
                            </td>
                        </tr>
                    </table>
                </form>
            </div>
        </div>
        <div class="step step-3" v-if="step===2">
            <img src="../../assets/img/money/icon_success.png" alt="">
            <p class="bold" style="color: black">恭喜您，您的卡券兑换成功，福分已存入您的账户</p>
            <div class="clearfix ac">
                <div class="step-3-btn fl ac bold cursor">
                    <router-link to="/money">查看我的福分</router-link>
                </div>
                <div class="step-3-btn fl ac bold cursor">
                    <router-link to="/">返回个人中心</router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    let display = {
        showRecommend: false,
        nav: 'hidden',
        header: 'fault'
    };
    import Coder from '../../../../components/VerifyCode'

    export default {
        name: "exchange",
        metaInfo: {
            title: '积分卡兑换-个人中心', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        created() {
            this.$store.dispatch('layout/Change', display);
            if (this.storage.hasItem('FFT')) {
                this.getMemberInfo()
            }
        },
        components: {
            Coder
        },
        data() {
            return {
                step: 1,
                account:'',
                pwd: '',
                sms_code:'',
                type:0,
                userinfo: {}
            }
        },
        mounted(){
            window.onpopstate = ()=>{
                this.back()
            }
        },
        computed:{
            cardcheck(){
                if (this.account &&this.pwd){
                    return true
                }else{
                    return false
                }
            },
            allInput(){
                if (this.account&&this.pwd&&this.sms_code) {
                    return true
                }else{
                    return false
                }
            }
        },
        methods: {
            back(){
                if (this.step === 1 ){
                    return
                } else {
                    this.step -= 1
                }
            },
            getMemberInfo() {
                this.yum('api/member/getMemberInfo').then(res => {
                    if (res.data.status === 200) {
                        this.userinfo = res.data.data
                    }
                })
            },
            CheckUserInfo() {
                if (!this.account){
                    this.bus.$emit('notice','请检查卡号是否有误?');
                }
                if (!this.pwd){
                    this.bus.$emit('notice','请检查密码是否有误?');
                }
                if(this.account&&this.pwd&&this.sms_code){
                    let data = {
                        account:this.account,
                        password:this.pwd,
                        search:0
                    }
                    this.yum('api/member/cardCharge',data).then(res=>{
                        if  (res.data.status ===200 ){
                            this.bus.$emit('notice',res.data.msg);
                            this.$router.go(-1)
                        }else{
                            this.bus.$emit('notice',res.data.msg);
                        }
                    })
                }
                // this.$router.push({
                //     query: {
                //         step: 2
                //     }
                // });
                // this.step = 2
            }
        }
    }
</script>

<style scoped lang="stylus">
    .present
        min-width 100%
        margin 40px 0 0 0
        position relative
        &.router
            margin-left 0

    ul.routes
        position relative
        li
            position relative
            display inline-block
            &:after
                content '>'
                margin 0 10px
            &:last-child:after
                display none

    .step
        width 720px
        margin 55px auto 70px
        li
            display inline-block
        &-icon
            display inline-block
            width 35px
            height 35px
            line-height 35px
            font-size 20px
            color white
            background-color #dcdcdc
            margin 0 3px
            border-radius 50%
            &.active
                background-color #0cbc5a
        &-line
            display inline-block
            margin 0 3px
        &-form
            background-color #f2f2f2
            padding 20px 0 50px
            margin-top 50px
            color black
            h2
                font-size 20px
                margin 0 10px 50px
                padding-bottom 20px
                border-bottom 1px solid #ccc
            form
                width 480px
                margin 0 auto
            table
                width 100%
                tr
                    min-height 40px
                    line-height 40px
                    margin-bottom 20px
                    position relative
                    .net-step
                        font-size 16px
                        color white
                        background-color #d60073
                        border-radius 4px
                        &:hover
                            background-color #ff0077
                        &.disabled
                            background-color #ccc
                    input, textarea
                        padding 5px
                        width 100%
                        outline none
                        border 1px solid #ccc
                        border-radius 4px
                        &:focus
                            border 1px solid #ff0000
                    input
                        height 40px
                    textarea
                        resize none
                        line-height 40px
                        height 80px
                    input.account-input
                        width 300px
                        border-radius 0
                        position absolute
                        bottom 0
                    a.button
                        background-color #dcdcdc
                        border 1px solid #ccc
                        position absolute
                        bottom 0
                        right 0
                        width 100px
                        height 40px
                        outline none

        &.step-3
            position relative
            img
                display block
                margin 80px auto 0
                width 80px
            p
                font-size 20px
                margin-top 50px
                text-align center
            .clearfix
                width 330px
                margin 50px auto 0
            .step-3-btn
                width 150px
                height 48px
                line-height 48px
                font-size 16px
                border-radius 4px
                background-color #e4e4e4
                a
                    display block
                    color inherit
                    font-weight inherit
                &:hover
                    background-color #ff0077
                    color white
                &:last-child
                    margin-left 30px
</style>